/* font Montserrat importing form Google font */
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");

/* Universal selector */
* {
  padding: 0;
  margin: 0;
  font-family: Montserrat, "sans-serif";
}

.app {
  height: 100vh;
  /* background-color: gold; */
  display: grid;
  place-content: center;
}

/* calculator container */
.app > .calculator {
  /* background-color: orange; */
  /* height: 18rem; */
  width: 16rem;
  padding: 3rem 4rem;
  border-radius: 2rem;

  box-shadow: -30px 20px 100px 10px #fb184e78, 30px -20px 100px 10px #0057ff78;
}

/* Calculator title */
.app > .calculator > .inputs > .title {
  font-family: Montserrat;
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 43.88px;
  text-align: center;
  color: #2b2b2b;
  background-color: white;
}

/* calculator input elements */
.app > .calculator > .inputs {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  /* background-color: aquamarine; */
}

/* Calculator input fields */
.app > .calculator > .inputs > .input-fields {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.6rem;
  /* background-color: orangered; */

  input {
    width: 80%;
    padding: 0.5rem;
    border: 2px solid #2b2b2b;
    font-family: Montserrat;
    font-size: 1rem;
    font-weight: 700;
    line-height: 29.26px;

    color: #2b2b2b;
    outline: none;

    &::placeholder {
      font-family: Montserrat;
      font-size: 1rem;
      font-weight: 700;
      line-height: 29.26px;

      color: #2b2b2b;
    }
  }
}

/* Calculator buttons */
.app > .calculator > .inputs > .input-fields > .btn-container {
  display: flex;
  justify-content: center;
  gap: 1rem;
  /* background-color: palevioletred; */
  width: 100%;

  .btn {
    display: grid;
    place-content: center;
    width: 2.6rem;
    aspect-ratio: 1/1;
    cursor: pointer;

    color: white;
    font-size: 1rem;
    font-weight: 900;
    border: none;
    background-color: #2b2b2b;
    
    &:hover{
      
      background-color: #4e4d4d;
    }
    &:active{
      transform: scale(0.96);
      background-color: #7a7979;
    }
  }
}

/* Calculator Message or Result  */
.app > .calculator > .inputs > .final-result {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  font-weight: 700;

  .success {
    color: #0057ff;
  }
  .error {
    color: red;
  }
  .resultElement {
    /* background-color: salmon; */
    text-align: center;
  }
}
